.btn {
    display: inline-block;
    padding: 1.5rem 1rem;
    border: 1px solid transparent;
    border-radius: 1rem;
    color: $clr-dark;
    font-weight: 550;
    position: relative;

    &.bg {
        &--primary {
            background: $clr-primary;
            color: $clr-light;
            border: 1px solid $clr-light;
            -webkit-transition: ease-out 0.4s;
            -moz-transition: ease-out 0.4s;
            transition: ease-out 0.4s;

            &::before {
                content: "";
                width: 0%;
                height: 100%;
                background-color: $clr-light;
                transform-origin: left;
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                -webkit-transition: ease-out 0.4s;
                -moz-transition: ease-out 0.4s;
                transition: ease-out 0.4s;
                z-index: -1;
                border-radius: 1rem;
            }

            &:hover::before {
                -webkit-transition: ease-out 0.4s;
                -moz-transition: ease-out 0.4s;
                transition: ease-out 0.4s;
                width: 100%;
                height: 100%;
            }

            &:hover {
                -webkit-transition: ease-out 0.4s;
                -moz-transition: ease-out 0.4s;
                transition: ease-out 0.4s;
                color: $clr-primary;
                border: 1px solid $clr-primary;
                height: 100%;
                z-index: 999;
            }
        }

        &--light {
            background: $clr-light;
            color: $clr-primary;
            border: 1px solid $clr-primary;

            &::before {
                content: "";
                width: 0%;
                height: 100%;
                background-color: $clr-primary;
                transform-origin: left;
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                transition: 0.5s ease;
                z-index: -1;
                border-radius: 1rem;
            }

            &:hover::before {
                transition: 0.5s ease;
                width: 100%;
            }

            &:hover {
                transition: 0.5s ease;
                color: $clr-light;
                border: 1px solid $clr-light;
                z-index: 999;
            }
        }
        &--bnnrlight {
            background: $clr-light;
            color: $clr-primary;
            border: 1px solid $clr-primary;
            border-radius: 4px;

            &::before {
                content: "";
                width: 0%;
                height: 100%;
                background-color: $clr-primary;
                transform-origin: left;
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                transition: 0.5s ease;
                z-index: -1;
                border-radius: 4px;
            }

            &:hover::before {
                transition: 0.5s ease;
                width: 100%;
            }

            &:hover {
                transition: 0.5s ease;
                color: $clr-light;
                border: 1px solid $clr-light;
                z-index: 999;
            }
        }
        &--bnnrblue {
            background: $clr-light;
            color: $clr-light;
           
            border-radius: 4px;

            &::before {
                content: "";
                width: 0%;
                height: 100%;
                background-color: $clr-light;
                transform-origin: left;
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                transition: 0.5s ease;
                z-index: -1;
                border-radius: 4px;
            }

            &:hover::before {
                transition: 0.5s ease;
                width: 100%;
            }

            &:hover {
                transition: 0.5s ease;
                color: $clr-dark;
                
                z-index: 999;
            }
        }

        
    }
}
