* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  line-height: 1.4;
  font-family: "Raleway", sans-serif;
}

.container {
  max-width: 140rem;
  padding: 1.5rem;
  margin: 0 auto;
}

/*COMPONENTS*/
.header {
  background: hsl(294, 54%, 27%);
  position: relative;
  position: fixed;
  width: 100%;
  z-index: 999;
}
.header .container {
  transition: 0.5s ease;
}
.header.white {
  background: transparent;
}
.header.white .container {
  background: blue;
  border-radius: 50px;
  margin-top: 5rem;
  transition: 0.5s ease;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .branding {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.header .branding img {
  width: 4rem;
}
.header .branding h3 {
  color: hsl(0, 0%, 100%);
  font-size: 3rem;
}
.header .branding ul {
  display: none;
}
.header__nav {
  position: absolute;
  top: 0;
  right: -100rem;
  width: 0%;
  height: 100vh;
  transition: 0.5s ease-out;
  background: hsl(0, 0%, 100%);
}
.header__nav.open {
  width: 100%;
  transition: 0.5s ease;
  right: 0rem;
}
.header__nav img {
  width: 15rem;
  padding: 2rem;
}
.header__nav ul.nav {
  display: flex;
  flex-direction: column;
  margin-top: 3rem;
}
.header__nav ul.nav li {
  padding: 2rem;
}
.header__nav ul.nav li a {
  font-size: 2rem;
  color: hsl(0, 0%, 0%);
  display: flex;
  justify-content: space-between;
}
.header__nav ul.nav li a i {
  color: hsl(0, 0%, 0%);
  transition: 0.2s ease;
}
.header__nav ul.nav li a i.open {
  transform: rotate(90deg);
  transition: 0.2s ease;
}
.header__nav ul.nav ul.drop {
  background: hsl(0, 0%, 100%);
  margin: 0 3rem;
  max-height: 0rem;
  overflow: hidden;
  transition: 0.5s ease;
}
.header__nav ul.nav ul.drop li a {
  font-size: 1.5rem;
}
.header__nav ul.nav ul.drop.open {
  max-height: 50rem;
  transition: 0.5s ease;
}
.header__nav__btn {
  position: absolute;
  bottom: 3rem;
  width: 100%;
  background: hsl(0, 0%, 100%);
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 2rem;
  gap: 2rem;
}
.header__nav__btn a {
  font-weight: 600;
  justify-content: center;
  align-items: center;
}
.header__actions {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.header__actions a i {
  color: hsl(0, 0%, 100%);
}
.header__actions .toggle__menu {
  z-index: 999;
}
.header__actions .toggle__menu span {
  width: 1.5rem;
  height: 0.2rem;
  background-color: hsl(0, 0%, 100%);
  display: block;
  border-radius: 5rem;
}
.header__actions .toggle__menu span:nth-child(2) {
  margin: 0.3rem 0;
}
.header__actions .toggle__menu.open span:first-child {
  background-color: hsl(0, 0%, 0%);
  width: 1.5rem;
  height: 0.2rem;
  display: block;
  border-radius: 5rem;
  transform: translateY(5.5px) rotate(45deg);
  transition: 0.5s ease;
}
.header__actions .toggle__menu.open span:nth-child(2) {
  opacity: 0;
  background-color: hsl(300, 62.16%, 14.51%);
}
.header__actions .toggle__menu.open span:last-child {
  width: 1.5rem;
  background-color: hsl(300, 62.16%, 14.51%);
  transform: translateY(-5px) rotate(-45deg);
  transition: 0.5s ease;
}
.header__actions .toggle__menu.open::after {
  transition: all 0.5s;
}
.header__actions__btn {
  display: none;
}
.header__actions__btn a.sign {
  display: none;
}
@media screen and (min-width: 940px) {
  .header__nav {
    display: none;
  }
  .header .branding ul {
    display: flex;
    gap: 2rem;
    margin-left: 5rem;
  }
  .header .branding ul li {
    transition: 0.5s ease;
  }
  .header .branding ul li a {
    color: hsl(0, 0%, 100%);
    font-weight: 600;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  .header .branding ul li a i {
    font-size: 1rem;
  }
  .header .branding ul li:hover {
    transform: scale(1.1);
    transition: 0.5s ease;
  }
  .header__actions .toggle__menu {
    display: none;
  }
  .header__actions__btn {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  .header__actions__btn a.sign {
    color: hsl(0, 0%, 100%);
  }
}

.btn {
  display: inline-block;
  padding: 1.5rem 1rem;
  border: 1px solid transparent;
  border-radius: 1rem;
  color: hsl(0, 0%, 0%);
  font-weight: 550;
  position: relative;
}
.btn.bg--primary {
  background: hsl(294, 54%, 27%);
  color: hsl(0, 0%, 100%);
  border: 1px solid hsl(0, 0%, 100%);
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
}
.btn.bg--primary::before {
  content: "";
  width: 0%;
  height: 100%;
  background-color: hsl(0, 0%, 100%);
  transform-origin: left;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
  z-index: -1;
  border-radius: 1rem;
}
.btn.bg--primary:hover::before {
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
  width: 100%;
  height: 100%;
}
.btn.bg--primary:hover {
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
  color: hsl(294, 54%, 27%);
  border: 1px solid hsl(294, 54%, 27%);
  height: 100%;
  z-index: 999;
}
.btn.bg--light {
  background: hsl(0, 0%, 100%);
  color: hsl(294, 54%, 27%);
  border: 1px solid hsl(294, 54%, 27%);
}
.btn.bg--light::before {
  content: "";
  width: 0%;
  height: 100%;
  background-color: hsl(294, 54%, 27%);
  transform-origin: left;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.5s ease;
  z-index: -1;
  border-radius: 1rem;
}
.btn.bg--light:hover::before {
  transition: 0.5s ease;
  width: 100%;
}
.btn.bg--light:hover {
  transition: 0.5s ease;
  color: hsl(0, 0%, 100%);
  border: 1px solid hsl(0, 0%, 100%);
  z-index: 999;
}
.btn.bg--bnnrlight {
  background: hsl(0, 0%, 100%);
  color: hsl(294, 54%, 27%);
  border: 1px solid hsl(294, 54%, 27%);
  border-radius: 4px;
}
.btn.bg--bnnrlight::before {
  content: "";
  width: 0%;
  height: 100%;
  background-color: hsl(294, 54%, 27%);
  transform-origin: left;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.5s ease;
  z-index: -1;
  border-radius: 4px;
}
.btn.bg--bnnrlight:hover::before {
  transition: 0.5s ease;
  width: 100%;
}
.btn.bg--bnnrlight:hover {
  transition: 0.5s ease;
  color: hsl(0, 0%, 100%);
  border: 1px solid hsl(0, 0%, 100%);
  z-index: 999;
}
.btn.bg--bnnrblue {
  background: hsl(0, 0%, 100%);
  color: hsl(0, 0%, 100%);
  border-radius: 4px;
}
.btn.bg--bnnrblue::before {
  content: "";
  width: 0%;
  height: 100%;
  background-color: hsl(0, 0%, 100%);
  transform-origin: left;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.5s ease;
  z-index: -1;
  border-radius: 4px;
}
.btn.bg--bnnrblue:hover::before {
  transition: 0.5s ease;
  width: 100%;
}
.btn.bg--bnnrblue:hover {
  transition: 0.5s ease;
  color: hsl(0, 0%, 0%);
  z-index: 999;
}

/*PAGES*/
.banner {
  background: hsl(294, 54%, 27%);
}
.banner__wrapper {
  padding-top: 10rem;
}
.banner__btn {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.banner__btn a {
  text-align: center;
}
.banner__btn a:last-child {
  background: #4285f4;
  border: 0px;
  text-align: center;
}
.banner__btn a:last-child img {
  width: 4.5rem;
  height: 4.5rem;
  object-fit: cover;
  position: absolute;
  align-content: center;
  padding: 1rem;
  background: hsl(0, 0%, 100%);
  left: 0;
  top: 0;
  bottom: 0;
}
.banner__text {
  text-align: center;
  padding-bottom: 5rem;
}
.banner__text h1 {
  color: hsl(0, 0%, 100%);
  font-size: 4rem;
  font-weight: bold;
}
.banner__text h1 span {
  color: #ecb22e;
}
.banner__text p {
  color: hsl(0, 0%, 100%);
}
.banner video {
  width: 100%;
  height: 400px;
  object-fit: cover;
  padding-top: 10rem;
  position: unset;
  bottom: 0;
}
@media screen and (min-width: 940px) {
  .banner .banner__wrapper {
    display: flex;
    flex-direction: row;
  }
  .banner .banner__text {
    text-align: left;
    order: 1;
  }
  .banner .banner__text h1 {
    max-width: 100rem;
    font-size: 6rem;
  }
  .banner .banner__text p {
    max-width: 45rem;
  }
  .banner .banner__btn {
    flex-direction: row;
  }
  .banner .banner__btn a {
    padding-top: 19px;
    padding-right: 40px;
    padding-bottom: 20px;
    padding-left: 40px;
  }
  .banner .banner__video {
    width: 100%;
    height: 400px;
    object-fit: cover;
    position: absolute;
  }
}